
.todo-add {
    margin: 10px 0;
}
.todo-title {
     position: relative;
}
.todo-title input {
    width: 100%;
    height: 50px;
    border-left: none;
    border-right: none;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    text-indent: 20px;
    line-height: 50px;
    font-size: 20px;
    outline: none;
}
.todo-title .clear-input {
    position: absolute; 
    color: white;
    background-color: #0EBF5A;
    height: 20px;
    width: 20px;
    line-height: 16px; 
    text-align: center;
    font-size: 20px;
    border-radius: 50%;
    top: 20px; 
    right: 20px;
}
.todo-detail {
    margin-left: 10px;
    background-color: white;
}
.add-detail-item {
    color: #0EBF5A;
    font-size: 18px;
    line-height: 40px;
    text-indent: 10px;
}
.add-detail-item:before {
    content: "+";
    font-size: 25px;
}
.detail-save {
    height: 50px;
    background-color: #0EBF5A;
    border: none;
    border-radius: 5px;
    color: white;
    width: 90%;
    position: fixed;
    bottom: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-size: 18px;
}
.err-tip {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    width: 250px;
}
.err-tip .err-title {
    margin: 0;
    font-size: 20px;
}
.err-tip .err-msg {
    margin-top: 10px; 
    color: #8A778A;
}
.err-tip .err-confirm {
    margin: 20px 0 0 0;
    text-align: right;
    color: #0EBF5A;
}



